import React, { Component } from 'react';


class Footer extends Component {
  render() {
    return (
        <footer className="footer">
        <div className="container">
          <span className="text-muted">© Copyright RTech 2018</span>
        </div>
      </footer>
    );
  }
}

export default Footer;



